.container > div > div {
  display: flex;
  align-items: center;
  justify-content: center;
}
.outerDiv{
  width:100%;
  height:600px;
  position: relative;
  overflow: hidden;
  background: url('../../../../assets/midParallaxBanner/casual.jpg');
  
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment:fixed ;
  overflow: hidden;
  opacity: 1;
  
}
.galaxyDiv{
  display: flex;
  margin: 0;
  padding: 0;
  justify-content: space-evenly;
  flex-direction: row;
  flex-wrap: wrap;
  height: 40%;
  background-color: whitesmoke;
  width: 110%;
  border: black 1px solid;
  position: relative;
  top: 37%;
  right: 10%;
  transform-style: preserve-3d;
  
}

.galaxyDiv button{
  cursor: pointer;
  z-index: 10;
  align-self: center;
}
.CollectionDiv{
  position: relative;
  top: 25%;
  color: whitesmoke;
  text-shadow: 4px 5px 4px black;
  background-color: transparent;
}
.CollectionDiv p:nth-child(1){
font-size: 50px;
font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
font-weight: bold;
}

.CollectionDiv p:nth-child(2){
font-size: 30px;
font-family: cursive;
font-weight: lighter;
}
.quotesDiv{
width: 33%;
font-size: 13px;
padding: 10px;

}
.quotesDiv span:nth-child(1){
font-size: 30px;margin: 5px;
font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
 
}
.galaxyDiv button {
  --c:white
  ;
  position: relative;
  top: 20%;
  right: 50%;
  color: black;
  font-size: 16px;
  border: 1px solid black;
  border-radius: 50px 50px;
  width: 12em;
  height: 3.3em;
  text-transform: uppercase;
  font-weight: bold;
  font-family: sans-serif;
  letter-spacing: 0.1em;
  text-align: center;
  line-height: 3em;
  overflow: hidden;
  z-index: 10;
  transition: 0.5s;
  margin: 1em;
}

.galaxyDiv button:hover {
  color: white;
  background-color: black;
}

